import {TextArea} from 'vue-ydui/dist/lib.rem/textarea';
import router from '@/router/index';
export default{
	data(){
		return{
			signRemark:this.$route.params.signRemark
		}
	},
	methods:{
		getSignRemark(){
			router.push({name:'oaSignIn',params:{signRemark:this.signRemark}});
		},
		cancel() {
			router.go(-1);
		}
	}
}




